:root{
  --white : rgb(240,217,181);
  --black : rgb(181,136,99);
  --red : rgb(235, 97, 80);
  --green : rgb(97, 235, 80);

  background-color: #282c34;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center; 
}
.buttonContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 0.3rem;
}
.boardContainer{
  display: flex;
  flex-direction: column;
  align-items: center; 
  padding: 0.3rem;
}

.board {
  padding: 0.3rem;
  display: grid;
  width: 85vh;
  aspect-ratio: 1;
}
@media (max-width: 50em) {
  .board {
    width: 50vh;
  }
  .boardContainer{
    padding: 2rem;
  }
  .buttonContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}

.input {
  font-size: 1.3rem;
  text-align: center;
  border: 2px solid var(--black);
  border-radius: 5px;
  outline: none;
  background-color: transparent;
  color: var(--white);
  transition: all 0.1s ease;
}
.input::-webkit-inner-spin-button, 
.input::-webkit-outer-spin-button { 
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
}
 .input:hover, .input:focus {
  border: 2px solid var(--white);
  scale: 1.01;
}

.button{
  cursor: pointer;
  background-color: var(--black);
  color: var(--white);
  border: 1px solid var(--white);
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  font-size: 1.5em;
  font-family: 'Roboto', sans-serif;
  transition: all 0.1s ease;
}
.button:hover{
  background-color: var(--white);
  color: var(--black);
  border: 1px solid var(--black);
  scale: 1.02;
}

.square {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;
  border: 0px;
  box-shadow: 0;
  transition: all 0.1s ease;
}
.square:hover {
  /* border: 2px solid rgb(34, 34, 34); */
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  z-index: 1;
  scale: 1.05;
  transition: all 0.1s ease;
}
.square img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* opacity: 0; */
  /* scale: 0; */
  transition: all 0.2s ease;
}


.highlight{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: var(--red);
  transition: all 0.2s ease;
}

